<template>
    <div id="app">
        <div class="sign-bg">
			<div class="signup-tabs">
				<div class="signup-container">
					<div class="signup-tab">
						<span>账号密码登录</span>
						<i class="icon-tri"></i>
					</div>
				</div>
			</div>
		</div>
		<div class="signup-form">
			<input type="" name="" v-model="nickname" placeholder="请输入昵称" class="input-sign">
			<input type="" name="" v-model="phone" placeholder="请输入手机号" class="input-sign">
			<div class="authcode-input">
				<input type="" name="" v-model="authcode" placeholder="请输入验证码" class="input-sign">
				<div class="authcode-trigger" v-if="code_sent">重新获取{{ timer }}</div>
                <div class="authcode-trigger" v-else v-on:click="sendcodeHandler">获取验证码</div>
			</div>
			<input type="password" name="" v-model="password" placeholder="请输入密码" class="input-sign">
			<button class="button-default button-signup" v-on:click="signupHandler">注册</button>
			<a href="signin.html" class="login-entry">或“登录”</a>
		</div>
		<transition name="fade">
			<Toast v-bind:msg="toastText" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>

<script>
import Hello from '../../components/Hello'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toastText: '',
    		nickname: '',
	    	phone: '',
	    	authcode: '',
	    	password: '',
            timer: 60,
            code_sent: false
    	}
    },
    methods:{
    	signupHandler: function(){
    		var that = this;
    		var nickname = utils.trim(that.nickname);
    		var phone = utils.trim(that.phone);
    		var authcode = utils.trim(that.authcode);
    		var password = utils.trim(that.password);
    		if(nickname == '' || nickname == undefined){
    			that.toastText = '昵称不能为空！';
    			that.showToast();
    			return;
    		}
    		if(phone == '' || phone == undefined){
    			that.toastText = '手机号不能为空！';
    			that.showToast();
    			return;
    		}
    		if(authcode == '' || authcode == undefined){
    			that.toastText = '验证码不能为空！';
    			that.showToast();
    			return;
    		}
    		if(password == '' || password == undefined){
    			that.toastText = '密码不能为空！';
    			that.showToast();
    			return;
    		}
            store.register(nickname, phone, authcode, password).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    location.href = store.site_links.usertype
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
    	},
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 3000);
    	},
    	sendcodeHandler: function(){
    		var that = this;
    		var phone = that.phone;
    		if(phone == '' || phone == undefined || !utils.checkphone(phone)){
    			that.toastText = '请输入正确的手机号码！';
    			that.showToast();
    			return;
    		}
            that.code_sent = true;
            store.authcode('login', phone).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    that.code_sent = true;
                    that.$nextTick(function(){
                        that.initTimer();
                    });
                    that.toast_text = '验证码已经发送，请查看手机短信！';
                    that.showToast();
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
    	},
        initTimer:function(){
            var that = this;
            that.timerInterval = setInterval(function(){
                if(that.timer>0){
                    that.timer = that.timer-1;
                }else{
                    that.stopTimer();
                }
            }, 1000);
        },
        stopTimer: function(){
            var that = this;
            clearInterval(that.timerInterval);
            that.timer = 60;
        }
    },
    components: {
    	Hello,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/sign.css';
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
</style>



